import {Card, CardColumns, Container, Row} from "react-bootstrap";
import * as React from "react";

interface Props {

}
interface Certificate {
  completionDate: Date;
  title: string,
  imageUrl: string,
  pdfUrl: string
}
const CERTIFICATES: Certificate[] = [
  {
    title: 'HTML5',
    completionDate: new Date('2019-09-22'),
    imageUrl: '/static/cert-1014-15680677.jpg',
    pdfUrl: '/static/cert-1014-15680677.pdf',
  },
  {
    title: 'CSS',
    completionDate: new Date('2019-09-30'),
    imageUrl: '/static/cert-1023-15680677.jpg',
    pdfUrl: '/static/cert-1023-15680677.pdf',
  }
];

function CertificateList(props: Props) {
  const certificateListItems = CERTIFICATES.map((certificate: Certificate) =>
    <Card style={{width: '18rem'}}>
      <Card.Img variant="top" src={certificate.imageUrl} />
      <Card.Body>
        <Card.Title>{certificate.title}</Card.Title>
        <Card.Subtitle>Completed on {certificate.completionDate.toLocaleDateString()}</Card.Subtitle>
        <Card.Link href={certificate.pdfUrl}>Download PDF</Card.Link>
      </Card.Body>
    </Card>
  );

  return (
    <Container>
      <Row className="justify-content-md-center">
        <h2>Sololearn Certificates</h2>
      </Row>
      <Row>
        <p>
          <a href="https://www.sololearn.com/Profile/15680677/">View profile</a>
        </p>
      </Row>
      <Row>
        <CardColumns>
          {certificateListItems}
        </CardColumns>;
      </Row>
    </Container>
  );
}

export default CertificateList;
